import { useQuery } from '@tanstack/react-query';
import type { UseQueryOptions } from '@tanstack/react-query';
import { apiService } from '../services/api';
import type { CourseWithClasses, GetCoursesParams } from '../types';

export function useCourses(
  params?: GetCoursesParams,
  options?: Omit<UseQueryOptions<CourseWithClasses[], Error>, 'queryKey' | 'queryFn'>
) {
  const key = ['courses', params?.kode_prodi ?? null, params?.semester ?? null, params?.include_classes ?? null] as const;
  return useQuery<CourseWithClasses[], Error>({
    queryKey: key,
    queryFn: () => apiService.getCourses(params),
    ...(options ?? {}),
  });
}
